@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');



:root{
    --color-text: hsl(0, 0%, 0%);
    --color-light-blue: #70b3ff;
    --color-lighter-blue: #70b3ffdc;
    --bg-body: #f8f6fe;
    --bg-container: #fff;

    /* size and typography */
    --body-font: 'Rubik', sans-serif;
    --fs-h1: 2.25rem;
    --fs-h2: 1.5rem;
    --fs-h3: 1.25rem;
    --fs-text-regular: 1rem;
    --fs-text-small: 0.875rem;
    --fs-text-smaller: 0.813rem;

    /* font weight */
    --fw-500: 500;
    --fw-600: 600;
    --fw-700: 700;

    /* cox shadow */
    --box-shadow: 0 5px 20px 0 rgba(0,0,0,0.2);

    /* border */
    --border-radius: 20px;

}

/* global settings */
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html{
    scroll-behavior: smooth;
}
body,
button,
input,
textarea{
    font-family: var(--body-font);
    font-size: var(--fs-text-regular);
}
body{
    background-color: var(--color-lighter-blue);
    background-color: var(--bg-body);
    color: var(--color-text);
    line-height: 1.7;
}
h1, h2, h3{
    color: var(--color-text);
    font-weight: var(--fw-600);
    line-height: 1.2;
}
ul{
    list-style: none;
}
a{
    text-decoration: none;
}
button{
    cursor: pointer;
    border: none;
    outline: none;
}
img{
    max-width: 100%;
    height: auto;
    vertical-align: middle;
}

/* reusable classes */
.container{
    max-width: 1080px;
    padding: 20px 15px;
    margin: auto;
}
.grid{
    display: grid;
}
.section{
    padding-top: 7rem;
    padding-bottom: 4rem;
}
.section__title{
    font-size: var(--fs-h1);
    font-weight: var(--fw-700);
    position: relative;
    margin-left: 0.875rem;
    margin-bottom: 3.75rem;
}

.btn{
    display: inline-block;
    background-color: var(--color-light-blue);
    color: var(--bg-container);
    line-height: 1;
    letter-spacing: 2px;
    border-radius: 1.875rem;
    padding: 0.75rem 2rem;
    box-shadow: 3px 3px 15px rgba(0,0,0,0.2),
                -3px -3px 15px rgba(245,245,245,0.5);
    border: 1px solid transparent;
    font-weight: var(--fw-700);
    transition: .3s linear;
    cursor: pointer;
}

@keyframes btn-push {
    50%{
        transform: scale(0.8);
    }
    100%{
        transform: scale(1);
    }
}
.btn:hover{
    box-shadow: 6px 6px 0 rgba(0,0,0,0.8);
    transform: translateY(-10px);
}

/* responsive typography */
@media screen and (min-width: 500px) {
    :root{
        --fs-h1: 4rem;
        --fs-h2: 3.25rem;
        --fs-h3: 1.45rem;
        --fs-text-regular: 1.25rem;
        --fs-text-small: 1rem;
        --fs-text-smaller: 0.835rem;
    }
}