.work__filters{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: .8rem;
    margin-bottom: 2.5rem;
}
.work__item{
    cursor: pointer;
    font-weight: var(--fw-700);
    transition: .3s linear;
}
.work__item:hover{
    color: var(--color-light-blue);
}
.work__container{
    grid-template-columns: repeat(auto-fit, minmax(14rem, 1fr));
    gap: 1.875rem;
}
.work__card{
    position: relative;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: var(--box-shadow);
    padding-bottom: 8px;
    background-color: rgba(255, 255, 255, 0.8);
}
.work__category{
    font-size: var(--fs-text-smaller);
    display: inline-block;
    margin-left: 1.2rem;
    color: rgba(0,0,0,0.6);
}
.work__title{
    font-size: var(--fs-h3);
    margin-bottom: 8px;
    margin-left: .8rem
}
.work__links{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.work__button{
    font-weight: var(--fw-500);
    font-size: var(--fs-text-small);
    padding: 0.75rem 1.2rem;
    margin-top: 15px;
    margin-bottom: 5px;
}

.work__img{
    object-fit: cover;
    display: block;
    width: 100%;
}

@media (min-width:439px) {
    .work__filters{
        display: flex;
        align-items: center;
        flex-wrap:nowrap;
        justify-content:flex-start;
        column-gap: 1.875rem;
    }
}